import React from "react";
import "./card.scss";
import "materialize-css/dist/css/materialize.min.css";
import "materialize-css/dist/js/materialize.min.js";

export const Card = (props) => {
  const { name, quantity, price, asset, code, issuer } = props.pokemons;
  return (
    <div className="card hoverable">
      <img alt="#" src={`./img/${name}.png`} />
      <div>{name}</div>
      <a
        className="black-text"
        href={`https://stellar.expert/explorer/public/asset/${name}-${asset}`}
      >
        Quantity:
        <span className="blue-text"> {quantity}</span>
      </a>
      <p>
        Price: {price}
        <span> {code}</span>
      </p>
      <a
        href={`https://stellarport.io/exchange/${asset}/${name}/${issuer}/${code}/`}
      >
        <button className="waves-effect green btn pulse">Buy!</button>
      </a>
    </div>
  );
};
